<template>
    
        <base-container>
                <template v-slot:Experience >
                    <section v-for="(role,index ) in RolesList" :key="role.id" 
                    class="skill" :style="getTransformStyle(index)" ref="sections" >
                    <h3>{{ role.Title }}</h3>
                    <p>{{ role.TitleDesc }}</p>
                    <h5>Lanaguages I speak</h5>
                    <p>{{ role.Languages }}</p>
                    <h5 v-if="role.id == 0">Frameworks I use</h5>
                    <p v-if="role.id == 0">{{ role.Frameworks }}</p>
                    <h5>Tools I use</h5>
                    <p >{{ role.Tools }}</p>
                    <h5>Years of Experience</h5>
                    <p>{{ role.Years }}</p>
                    </section>
                </template>


            
        </base-container>
        
    
</template>
<script>
import BaseContainer from './UI/BaseContainer.vue';
export default {
    components:
    {
        BaseContainer},
    inject:['RolesList'],
    mounted() {
    window.addEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll() {
      const windowHeight = window.innerHeight;
      this.$refs.sections.forEach((section, index) => {
        const sectionOffset = section.getBoundingClientRect().top;
        //console.log(-windowHeight , sectionOffset )
        if (sectionOffset < windowHeight / 1.2 && sectionOffset > -windowHeight / 1.2) {
          section.style.transform = 'translate(0,-100px )' ;
        } else {
          const translationX = index % 2 === 0 ? -200 : 200;
          section.style.transform = `translate(${translationX}% ,-100px )`;
        }
      });
    },
    getTransformStyle() {
        
      return {
        transform: 'translate(0,-100px)',
      };
    },
  },

}
    
</script>
<style scoped>
section{
        width: 42%;
        height: 500px;
        display: flex;
        flex-direction: column;
        justify-content: baseline;
        align-items: center;
        flex-wrap: 1;
        border: 1px solid #999;
        border-radius: 10px;
        box-shadow: 2px 4px 16px 4px #999;
        background-color: rgb(255, 255, 255);
       
        margin: 0 5px;
        padding: 3.5rem 2rem;
        transition: transform 0.8s ease-in;
    }

   
    
</style>