<template>
    <div class="bigc">
        <h1>Testimonials</h1>
        <testimonial-comp :value="ShowTestA" :valueb="ShowTestB">
            <template #test="slotProps">
                <div class="slotted-ele">
            <img :src="slotProps.item.TImg" alt="">
            <h3>{{ slotProps.item.TName }}</h3>
            <h3>{{ slotProps.item.TRole }}</h3>
            <p>{{ slotProps.item.TDesc}}</p>
        </div>        
            </template>
        </testimonial-comp>
        <BaseButton>
                    <template v-slot:Button>
                        <div class="btn_holder">
                        <button class="nav_btn" type="button" @click="GetTest('A')" ></button>
                        <button class="nav_btn" type="button"  @click="GetTest('B')" ></button>
                        </div>
                    </template>
                </BaseButton>
    </div>
</template>
<script>
import TestimonialComp from '@/components/TestimonialComp.vue';
import BaseButton from '@/components/UI/BaseButton.vue';
export default {
    props:{

    },
components:{TestimonialComp,BaseButton},
data(){
    return{
        ShowTestA : true,
        ShowTestB : false
    }
},
methods:{
    GetTest(Test){
        if(Test == 'B'){
            this.ShowTestB = false
            this.ShowTestA = true
            
        }
        else{
            this.ShowTestB = true
            this.ShowTestA = false
        }
    }
}
}
</script>
<style scoped>
.bigc{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 1rem;
    margin-top: 2rem;
}
img{
    width: 80px;
    height: 80px;
    border-radius: 50%;
    
  }
    .btn_holder{
        display: flex;
        justify-content: center;
        width: 20%;
        margin: auto;
    }
    .nav_btn{
        margin: 0 0.5rem;
    }
</style>