<template>
    <div class="cont">
        <base-headers>
            <template v-slot:fst_header>
                <h1>My Best Projects</h1>
                <p><a href="">Click here</a> to know more about my projects</p>
            </template>
                
        </base-headers>
            
        <BaseContainer>
            <template v-slot:Projects>
                <section class="project_infos" v-for=" ( project , index ) in ProjectsList"
                 :key="project.Pid" ref="project" :style="GetStyle(index)">
                    <h1 class="ptitle">{{ project.PTitle }}</h1>
                    <p class="pdes">Lorem ipsum dolor sit amet consectetur 
                        adipisicing elit. Ipsa atque in officiis iusto aut ipsam praesentium ipsum sint?
                        Architecto quas pariatur placeat minus
                        voluptatum veniam incidunt laborum blanditiis ea beatae!{{ project.PDescription }}</p>
                </section>
        </template>
        </BaseContainer>
        
            <purple-button>
                <template v-slot:ButtonB>
                        <button class="second_btn">Hire me for your Project</button>
                </template>
            </purple-button>
        
    </div>
    
</template>
<script>
import BaseContainer from './UI/BaseContainer.vue';
import BaseHeaders from './UI/BaseHeaders.vue';
import PurpleButton from './UI/PurpleButton.vue';
export default {
    components:{
        BaseContainer,BaseHeaders,PurpleButton
    },
    inject:['ProjectsList'],
    mounted(){
        window.addEventListener('scroll' , this.handlescroll)
    },
    methods:{
        handlescroll(){
            const WindowHeight = window.innerHeight
             
            this.$refs.project.forEach((project , index) => {
                const project_pos = project.getBoundingClientRect().top
                //console.log(index + ' ' + project_pos)
                 if(project_pos < WindowHeight / 1.5 && project_pos > -WindowHeight/2.5){
                    project.style.transform = 'translateX(0)'
                 }
                 else{
                    const Value = 400
                    const translationX = index < 3 ? -Value : Value;
                    project.style.transform = `translate(${translationX}% )`
                 }
            })
        },
        GetStyle(){
            return{
                transform: 'translateX(0)'
            }
        }
    }
}
</script>
<style scoped>
    section{
        width: 30%;
        height: 15rem;
        display:flex;
        flex-direction: column;
        justify-content: center;
        flex-wrap: nowrap;
        background-color: white;
        margin: 4px 5px;
        padding: 4px 3px;
        border: 1px solid #999;
        border-radius: 10px;
        box-shadow: 2px 4px 16px 4px #999;
        transition:transform 0.8s ease-in;
    }
    a{
        text-decoration: none;
        color: red;
    }
    .cont{
        border-bottom: 1px solid rgba(0, 0, 0, 0.314);
        box-shadow: -1px 7px 5px -3px #999;
    }
    
    
    
</style>