<template>
    <div class="nav">
        <nav class="nav-bar">
            <div class="logo">
                <img src="..\assets\navigation\symbol.png" alt="">
            </div>
            <ul class="links">
                <li class="link-item"><a @click="scrollToSection('skills-section')" >Skills</a></li>
                <li class="link-item"><a @click="scrollToSection('projects-section')" >Projects</a></li>
                <li class="link-item"><a @click="scrollToSection('testimonials-section')" >Testimonials</a></li>
                <li class="link-item"><a class="button_link" @click="scrollToSection('')" >
                    <BaseButton>
                    <template v-slot:Button>
                        <button class="nav_btn" type="button">Contact Me</button>
                    </template>
                </BaseButton></a></li>
            </ul>
        
        </nav>
    </div>
</template>
<script>
import BaseButton from '@/components/UI/BaseButton.vue';
export default {
    
components:{
    BaseButton
},
methods: {
    scrollToSection(refName) {
        
    const targetSection = document.getElementById(refName);
    console.log(targetSection)
    if (targetSection) {
        
      const offset = targetSection.offsetTop - 180; // Subtract 100px from the offset
      console.log(offset)
      targetSection.scrollIntoView({
        behavior: 'smooth'
      });
      console.log(targetSection.tagName)
      //return to fix
      if (refName != 'projects-section') {
        window.scrollTo({
        top:offset,
        behavior:'smooth'
      })
      }
      
    }
  }
},

  


}
</script>
<style scoped>

    .nav{
        width: 100%;
        height: 8rem;
        display: flex;
        align-items: center;
        margin: 0;
        background-color: #7f0ec6;
}
    .nav-bar{
        width: 100%;
        justify-content: center;
        display: flex;
        height: 6rem;
        
        
    }
    .links{
        width: 90%;
        padding: 0 1rem;
        display: flex;
        justify-content: right;
        list-style: none;
    }
    .link-item{
        margin: auto 0.6rem;
        cursor: pointer;
        transition: color 1s;
        
        
    }
    .link-item:hover , .link_item:active{
        
    }
    .logo{
        display: flex;
        align-items: center;
    }
    .logo img{
        
        width:50px;
        height: 50px;
    }
    
    
    a:hover:not(.button_link) , a:active:not(.button_link){
